import React from "react";
import ReactDOM from "react-dom";
import CalendarGrid from "./components/CalendarGrid";
import ReleaseList from "./components/ReleaseList";
import moment from "moment";
import "../css/calendar-grid.scss";
import { fieldValue } from "./utils";
import {
    FIELD_DISTRIBUTOR_LOGO,
    FIELD_DISTRIBUTOR_NAME,
    FIELD_DISTRIBUTOR_LONG_NAME,
    FIELD_RELEASE_WEEK,
    FIELD_HOLIDAY_NAME
} from "./field-types";

const RAW_DATE_FORMAT = "YYYYMMDD hh:mm:ss";
const DATE_FORMAT = "MMM DD";
let dimensionByName = {};

const headerRender = (config, data) => {
    let content;

    if (data.name === "dist_logo") {
        content = (
            <img src={data.value} alt={data.alt} /> 
        );
    } else {
        content = <span className="cg-title">{data.value}</span>;
    }

    return content;
};

const cellRender = (config, data) => {
    let content;
    if (data.name === "release_week") {
        const date = moment(data.value, RAW_DATE_FORMAT);
        if (date.isValid()) {
            content = (
                <React.Fragment>
                <span className="cg-title">{date.format(DATE_FORMAT)}</span><br/>
                <span className="cg-subtitle">{date.year()}</span><br/>
                { data.holiday && <span className="cg-event">{data.holiday}</span> }
                </React.Fragment>
            );
        } else {
            content = <span className="cg-title">{data.value}</span>;
        }
    } else if (data.name === "dist_name") {
        content = <span className="cg-title">{data.value}</span>;
    } else {
        content = <ReleaseList data={data.value || []} />;
    }
        
    return content;
};

const prepareData = (queryResults, config) => {
    if (queryResults.length > 0) {
        const distByName = {};
        const holidayByRelDate = {};

        //organize data by release_week and distributor_name  
        const dataByRelDateDist = queryResults.reduce((hash, row) => {
            let relDate = fieldValue(row, FIELD_RELEASE_WEEK);
            const distName = fieldValue(row, FIELD_DISTRIBUTOR_LONG_NAME) || fieldValue(row, FIELD_DISTRIBUTOR_NAME);
            const holiday = fieldValue(row, FIELD_HOLIDAY_NAME);
            const distLogo = fieldValue(row, FIELD_DISTRIBUTOR_LOGO);

            if (!moment(relDate, RAW_DATE_FORMAT).isValid()) {
                relDate = "Unset";
            }

            hash[relDate] = hash[relDate] || {};

            if (distName) {
                distByName[distName] = {
                    name: "dist_name",
                    value: distName,
                    logo: distLogo
                };
                holidayByRelDate[relDate] = holiday;

                hash[relDate][distName] = hash[relDate][distName] || [];
                hash[relDate][distName].push(row);
            }

            return hash;
        }, {});

        const distributors = Object.values(distByName).sort((a, b) => {
            if (a.value < b.value) return -1;
            if (a.value > b.value) return 1;
            return 0;
        });
        const distLogoColumns = hasField(FIELD_DISTRIBUTOR_LOGO) && [{ name: "blank", value: "", className: "dist-logo" }].concat(
            distributors.map(dist => {
                return {
                    name: "dist_logo",
                    value: dist.logo,
                    alt: dist.value,
                    className: "dist-logo"
                };
            })
        );
        const columns = [{ name: "release_week", value: "Week" }].concat(distributors);
        const releaseDates = Object.keys(dataByRelDateDist).sort();
        const rows = releaseDates.map(relDate => {
            return columns.map(col => {
                const isRelDate = col.name === "release_week";
                const cellData = {
                    name: isRelDate ? col.name : "releases",
                    value: isRelDate ? relDate : dataByRelDateDist[relDate][col.value]
                };

                if (isRelDate && holidayByRelDate[relDate]) {
                    cellData.holiday = holidayByRelDate[relDate];
                }

                return cellData;
            });
        });

        return distLogoColumns 
            ? [ distLogoColumns, columns ].concat(rows)
            : [ columns ].concat(rows);
    }

    return [];
};

const canRender = () => {
    return hasField(FIELD_RELEASE_WEEK)
        && (hasField(FIELD_DISTRIBUTOR_NAME) || hasField(FIELD_DISTRIBUTOR_LONG_NAME));
};

const hasField = (name) => {
    return !!dimensionByName[name];
};

looker.plugins.visualizations.add({
    options: {
        highlight: {
            type: "boolean",
            label: "Highlight",
            default: false
        },
        wrapTableContent: {
            type: "boolean",
            label: "Wrap Table Content",
            default: false
        }
    },
    create: function(element, config){
    },
    updateAsync: function(data, element, config, queryResponse, details, doneRendering){
        this.clearErrors();

        dimensionByName = config.query_fields.dimensions.reduce((hash, dimension) => {
            return { 
                ...hash, 
                [ dimension.name ]: {
                    ...dimension,
                    short_name: dimension.name.split(".").pop()
                }
            };
        }, {});

        if (canRender()) {
            const preparedData = prepareData(data, config);
            ReactDOM.render(
                <CalendarGrid 
                    className={config.wrapTableContent ? "wrap-table-content" : ""}
                    data={preparedData} 
                    highlight={config.highlight} 
                    cellRender={cellRender.bind(undefined, config)}
                    headerRender={headerRender.bind(undefined, config)}
                />, 
                element
            );
        } else {
            this.addError({
                title: "Required Dimensions Are Missing", 
                message: "The following dimensions are needed: Release Week and Distributor Name"
            });
        }
        
        doneRendering();
    }
});
